export default {
  data() {
    return {
      code: '',
      list: [1, 2, 3, 4, 5, 6, 7, 8, 9]
    };
  }, 
  onLoad(options){
	if(options.code){
		this.code=options.code
		this.sub()
	}
  },
  methods: {
    del() {
      let length = this.code.length;
      if (length) {
        this.code = this.code.substr(0, length - 1);
        console.log(length);
      }
    },
